/**
 * Fonts
 */

// Dependencies
@import 'config/fonts';
@import 'bourbon/app/assets/stylesheets/css3/font-face';
@import 'bourbon/app/assets/stylesheets/helpers/font-source-declaration';

// Declarations
@include font-face('Noto Sans', '#{$font-path}/noto-sans/NotoSans', normal, normal, (eot woff2 woff ttf));
@include font-face('Noto Sans', '#{$font-path}/noto-sans/NotoSans-Italic', normal, italic, (eot woff2 woff ttf));
@include font-face('Noto Sans', '#{$font-path}/noto-sans/NotoSans-Bold', bold, normal, (eot woff2 woff ttf));
@include font-face('Noto Sans', '#{$font-path}/noto-sans/NotoSans-BoldItalic', bold, italic, (eot woff2 woff ttf));

@include font-face('Noto Serif', '#{$font-path}/noto-serif/NotoSerif', normal, normal, (eot woff2 woff ttf));
@include font-face('Noto Serif', '#{$font-path}/noto-serif/NotoSerif-Italic', normal, italic, (eot woff2 woff ttf));
@include font-face('Noto Serif', '#{$font-path}/noto-serif/NotoSerif-Bold', bold, normal, (eot woff2 woff ttf));
@include font-face('Noto Serif', '#{$font-path}/noto-serif/NotoSerif-BoldItalic', bold, italic, (eot woff2 woff ttf));

// For Korean
@include font-face('Noto Sans CJK KR', '#{$font-path}/noto-cjk-kr/NotoSansCJKkr-Regular', normal, normal, (otf));
@include font-face('Noto Sans CJK KR', '#{$font-path}/noto-cjk-kr/NotoSansCJKkr-Regular', normal, italic, (otf));
@include font-face('Noto Sans CJK KR', '#{$font-path}/noto-cjk-kr/NotoSansCJKkr-Bold', bold, normal, (otf));
@include font-face('Noto Sans CJK KR', '#{$font-path}/noto-cjk-kr/NotoSansCJKkr-Bold', bold, italic, (otf));

// For Chinese
@include font-face('Noto Sans CJK TC', '#{$font-path}/noto-cjk-tc/NotoSansCJKtc-Regular', normal, normal, (otf));
@include font-face('Noto Sans CJK TC', '#{$font-path}/noto-cjk-tc/NotoSansCJKtc-Regular', normal, italic, (otf));
@include font-face('Noto Sans CJK TC', '#{$font-path}/noto-cjk-tc/NotoSansCJKtc-Bold', bold, normal, (otf));
@include font-face('Noto Sans CJK TC', '#{$font-path}/noto-cjk-tc/NotoSansCJKtc-Bold', bold, italic, (otf));

// For Arabic
@include font-face('Noto Naskh Arabic', '#{$font-path}/noto-ar/NotoNaskhArabic-Regular', normal, normal, (ttf));
@include font-face('Noto Naskh Arabic', '#{$font-path}/noto-ar/NotoNaskhArabic-Regular', normal, italic, (ttf));
@include font-face('Noto Naskh Arabic', '#{$font-path}/noto-ar/NotoNaskhArabic-Bold', bold, normal, (ttf));
@include font-face('Noto Naskh Arabic', '#{$font-path}/noto-ar/NotoNaskhArabic-Bold', bold, italic, (ttf));
