pre {
  color: #F1647C;
  background-color: #EEF3F7;
  display: block;
  padding: 16px 16px;
  border: 1px solid #D1D5D9;
  border-radius: 3px;
  max-width: 100%;
  overflow: hidden;
  overflow-x: auto; }

.code-block {
  color: #F1647C;
  display: inline-block;
  display: block;
  max-width: 100%;
  overflow: hidden;
  overflow-x: auto; }
  .code-block pre {
    color: inherit;
    background-color: transparent;
    margin: 0;
    padding: 16px 16px;
    border: none; }

/**
 * Layouts
 */
/*
 * Homepage Margin Fix
 * Removes the margins on flush elements at mobile sizes to keep
 * up with the larger max-width element
 */
.usa-grid {
  padding-left: 16px;
  padding-right: 16px;
  margin-left: auto;
  margin-right: auto; }
  @media screen and (min-width: 768px) {
    .usa-grid {
      padding-left: 24px;
      padding-right: 24px; } }

.layout--map {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse; }
  @media screen and (min-width: 960px) {
    .layout--map {
      height: 90vh;
      flex-direction: row; } }
  .layout--map .layout--map__sidebar {
    width: 100%; }
    @media screen and (min-width: 960px) {
      .layout--map .layout--map__sidebar {
        height: 90vh;
        overflow: hidden;
        overflow-y: auto; } }
  .layout--map .layout--map__map {
    height: 50vh;
    width: 100%; }
    @media screen and (min-width: 960px) {
      .layout--map .layout--map__map {
        height: 100%; } }

.page-wrapper,
.wrap {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1200px;
  min-height: 100%;
  min-width: 320px; }

.layout--columns {
  display: flex;
  flex-wrap: wrap; }
  .layout--columns > * {
    width: 50%; }

.layout--rows {
  display: flex;
  flex-wrap: wrap; }
  .layout--rows > * {
    height: 50%;
    width: 100%; }

.layout--gutter > * {
  width: 100%;
  margin-bottom: 24px; }

.layout--gutter > *:last-child {
  margin-bottom: 0; }

.layout--columns-gutter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  .layout--columns-gutter > * {
    margin-bottom: 24px;
    width: calc(50% - 12px); }
  .layout--columns-gutter > *:nth-last-child(1),
  .layout--columns-gutter > *:nth-last-child(2) {
    margin-bottom: 0; }

.layout--four-columns-gutter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  .layout--four-columns-gutter > *, .layout--four-columns-gutter:after {
    width: calc(25% - 12px); }
  .layout--four-columns-gutter > * {
    margin-bottom: 24px; }
  .layout--four-columns-gutter:after {
    content: '';
    margin-bottom: 0; }

.layout--three-columns-gutter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  .layout--three-columns-gutter > *, .layout--three-columns-gutter:after {
    width: calc(33.33% - 12px); }
  .layout--three-columns-gutter > * {
    margin-bottom: 24px; }
  .layout--three-columns-gutter:after {
    content: '';
    margin-bottom: 0; }

.layout--two-columns-gutter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  .layout--two-columns-gutter > *, .layout--two-columns-gutter:after {
    width: calc(50% - 12px); }
